<template>
    <div>
        <!-- 顶部 -->
        <div class="top">
            <img src="@/assets/icon/激活账号.png" alt="" class="imgAb">
            <img src="@/assets/icon/将军你好，请【登录】.png" alt="" class="imgAb" style="left:494px;top:11px" v-if="!isLogin">
            <img src="../assets/icon/你好，八嘎【退出】.png" alt="" class="imgUse" v-if="isLogin">
            <button class="loginButton" @click="login" v-if="!isLogin">
                <img src="@/assets/icon/登录.png" alt="">
            </button>
            <button class="loginButton" @click="Outlogin" v-if="isLogin">
                <img src="@/assets/icon/hgklsajf123【退出】.png" alt="">
            </button>
        </div>
        <!-- 登录页面 -->
        <div class="loginContent" v-if="isLogincontent">
            <div class="loginTitle">
                <p>这里没做随便登录一下</p>
            </div>
            <br>
            <span>账号</span><input type="text">
            <br>
            <br>
            <span>密码</span><input type="password">
            <br>
            <br>
            <button @click="loginSuccess">登录</button>
            <button @click="clearLogin">关闭登录页面</button>
        </div>
        <!-- 激活码模块 -->
        <div class="useCdkey" v-if="useCD">
            <div class="cdkeyTopBg" :style="cdkeyTopBg">
                <!-- 激活码选择 -->
                <img src="../assets/icon/一态.png" alt="" style="display:block">
                <button class="chooseBtn2" @click="chooseUseCD"></button>
            </div>
            <div class="cdkeyInput">
                <!-- 输入激活码 -->
                <input type="text" class="cdkeyInputContent" @click="inputFocus" @blur="inputBlur" v-model="iptContent">
                <img src="../assets/icon/请填写激活码.png" alt="" class="cdkeyInputText" v-show="!iptFocus">
                <img src="../assets/icon/填写激活码.png" alt="">
            </div>
            <div class="cdkeyYidun">
                <img src="../assets/icon/易盾验证占位 拷贝.png" alt="" class="cdkeyYidunText">
                <img src="../assets/icon/矩形 4 拷贝 2.png" alt="">
            </div>
        </div>
        <!-- 手机号激活模块 -->
        <div class="usePhone useCdkey" v-if="!useCD">
            <div class="cdkeyTopBg1" :style="cdkeyTopBg">
                <!-- 激活码选择 -->
                <button class="chooseBtn1" @click="UseCD1"></button>
                <img src="../assets/icon/2态.png" alt="" style="display:block" class="usePhoneImg">
            </div>
            <div class="cdkeyInput">
                <!-- 输入激活码 -->
                <input type="tel" class="cdkeyInputContent" @click="inputFocus" @blur="inputBlur" v-model="iptContent" maxlength="11">
                <img src="../assets/icon/请填写问卷预留手机号.png" alt="" class="cdkeyInputText" style="left:245px" v-show="!iptFocus">
                <img src="../assets/icon/填写激活码.png" alt="">
            </div>
            <!-- 易盾占位 -->
            <div class="cdkeyYidun">
                <img src="../assets/icon/易盾验证占位 拷贝.png" alt="" class="cdkeyYidunText">
                <img src="../assets/icon/矩形 4 拷贝 2.png" alt="">
            </div>
            <!-- 短信验证码框 -->
            <div class="cdkeyDuanxin">
                <button class="getDuanxin" @click="getDuanxin"></button>
                <input type="tel" maxlength="5" class="duanxinContent" v-model="duanxinContent">
                <img src="../assets/icon/获取短信验证码.png" alt="" class="cdkeyDuanxinText">
                <img src="../assets/icon/手机短信.png" alt="">
            </div>
        </div>
        <!-- 激活按钮 -->
        <div class="cdSuccess">
            <button class="cdSuccessBtn" @click="successBtn">
                <img src="../assets/icon/测试资格按钮.png" alt="">
            </button>
        </div>
    </div>
</template>
<script>
export default {
    name:'content1',
    data(){
        return {
            isLogin:false,
            isLogincontent:false,
            useCD:true,
            iptFocus:false,
            iptContent:'',
            duanxinContent:'',
            // 衬底图片
            cdkeyTopBg:{
                backgroundImage: "url(" + require("../assets/icon/衬底.png") + ")",
                backgroundRepeat: "no-repeat"
            }
        }
    },
    methods:{
        login(){
            this.isLogincontent = true
        },
        loginSuccess(){
            this.isLogin = true
            alert("不管你输了啥，反正登录成功了")
            this.isLogincontent = false

        },
        Outlogin(){
            this.isLogin = false
        },
        clearLogin(){
            this.isLogincontent = false
        },
        chooseUseCD(){
            this.useCD = false
            this.iptContent = ''
            this.iptFocus = false       
        },
        UseCD1(){
            this.useCD = true
            this.iptContent = ''     
            this.iptFocus = false       
        },
        inputFocus(){
            this.iptFocus = true 
                // console.log(this.iptFocus);
        },
        inputBlur(){
            if(this.iptContent === ''){
                this.iptFocus = false
                // console.log(this.iptFocus);
            }
        },
        getDuanxin(){
                alert('憨批八嘎，你的验证码是99999，快输入')
        },
        successBtn(){
            if(this.isLogin){
                if(this.useCD){
                    if(this.iptContent === 'hanpibaga'){
                        alert('哈哈哈哈，激活成功')
                        this.iptContent =''
                    }else{
                        alert('激活码是 hanpibaga')
                    }
                }else{
                    if(this.duanxinContent === '99999'){
                        alert('激活成功')
                        this.duanxinContent = ''
                        this.iptContent = ''
                    }else{
                        alert('输错了！')
                    }
                }
            }else{
                alert('请先登录')
            }
        }
    }
}
</script>
<style lang="stylus" scoped>
    .top
        position relative
        top 16px
        left 17px
        .imgAb 
            position absolute 
            // left 10px
            .loginText
                left 10px
        .imgUse
            position absolute 
            top 14px
            right 70px

        .loginButton
            position absolute 
            border none
            background none
            outline none
            height 40px
            width 70px
            left 620px
    .loginContent
        background white 
        height 350px
        width 300px
        position absolute 
        z-index 999
        left 30%
        top 50%
        .loginTitle
            width 100%
            height 30px
            text-align center
            line-height 30px
            font-weight bold
        span
            margin-left 35px
        button 
            margin-left 90px
            height 50px
            width 100px
            margin-bottom 10px
    .useCdkey
        position relative
        top 60px
        left 17px
        .cdkeyTopBg
            position absolute
            top 25px
            height 71px
            width 691px
            .chooseBtn2
                border none 
                height 70px 
                width 338px
                position absolute
                background none 
                left 350px
                top 0
                outline none
        .cdkeyInput
            position absolute
            top 120px
            .cdkeyInputText
                position absolute
                top 21px
                left 279px
            .cdkeyInputContent
                position absolute
                left 3px
                top 3px
                width 678px
                height 54px
                border none 
                background none
                color white 
                font-size 20px
                text-align center
                outline none 
                z-index 99
        .cdkeyYidun
            position absolute
            top 200px
            left 1px
            .cdkeyYidunText
                position absolute 
                top 10px
                left 290px
    .usePhone
        position relative
        top 60px
        left 17px
        .cdkeyTopBg1
            position absolute
            top 25px
            // left 100px
            height 71px
            width 691px
            .usePhoneImg
                position relative
                left 115px
            .chooseBtn1
                border none 
                height 70px 
                width 338px
                position absolute
                background none 
                outline none
                z-index 999
        .cdkeyDuanxin
            position absolute
            top 245px
            .cdkeyDuanxinText
                position absolute 
                top 11px
                right 12px
            .getDuanxin
                border none
                height 37px
                width 135px
                position absolute 
                top 0
                right 0
                background none
                outline none
                z-index 999
            .duanxinContent
                position absolute 
                top 3px
                z-index 999
                height 33px
                width 550px
                background none
                border none 
                outline none 
                color white 
                font-size 20px
                text-align center
    .cdSuccess
        position relative
        top 350px
        left 17px
        .cdSuccessBtn
            background none
            border none 
            outline none

</style>


