 <template>
     <div class="home">
        <div class="title">
            <img src="../assets/back/01.jpg" alt="">            
        </div>
        <div class="content">
            <div class="content-left content-display">
                <img src="../assets/back/02.png" alt="">            
            </div>
            <div class="content-home content-display">
                <Content class="textInput"></Content>    
                <img src="../assets/back/03.png" alt=""> 
            </div>
            <div class="content-right content-display">
                <img src="../assets/back/04.png" alt="">            
            </div>
        </div>
        <div class="footer" style="padding-bottom:-8px">
            <img src="../assets/back/05.png" alt="">            
        </div>    
     </div>
 </template>
 <script>
 import Content from '@/components/content'
 export default {
     name:'home',
     components:{
         Content
     }
 }
 </script>

 <style lang="stylus">
    .home
        max-width 1920px
        margin 0
        padding 0
        div
            margin-bottom -4px
            padding 0
        img 
            display block
        .content
            position relative
            height 481px
        .content-left,.content-home,.content-right
            position absolute
            top 4px 
        .content-home
            left 624px
            .textInput
                position absolute 
                
        .content-right
            left 1340px
        

        
 </style>



